.chevron-bottom:before {
  border-style: solid;
  border-width: 2px 2px 0 0;
  content: '';
  display: inline-block;
  height: 16px;
  width: 16px;
  top: 7px;
  left: 0;
  position: relative;
  transform: rotate(-45deg);
  vertical-align: top;
}

.chevron-right:before {
  border-style: solid;
  border-width: 1px 1px 0 0;
  border-color: #000;
  content: '';
  display: inline-block;
  height: 16px;
  width: 16px;
  top: 7px;
  left: 0;
  position: relative;
  transform: rotate(45deg);
  vertical-align: top;
}

.chevron-left:before {
  border-style: solid;
  border-width: 1px 1px 0 0;
  border-color: #000;
  content: '';
  display: inline-block;
  height: 16px;
  width: 16px;
  top: 7px;
  right: -6px;
  position: relative;
  transform: rotate(-130deg);
  vertical-align: top;
}

