html {
  font-family: "Roboto", sans-serif;
}

body {
  min-height: 100vh;
}

.home-container {
  .col-right {
    color: $primary;

    #browse-featured {
      a {
        font-size: 25px;
      }
    }

    .hidden-name {
      position: relative;
      h2 {
        font-weight: 500;
        font-size: 24px;
        opacity: 1;
        color: white;
        text-shadow: 0 0 5px rgb(0, 0, 0);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        visibility: visible;
        opacity: 0;
      }
    }

    .hidden-name:hover {
      img {
        opacity: 0.5;
      }
      h2 {
        opacity: 1;
      }
    }

    i {
      padding-right: 1vw;
      font-size: 25px;
    }
  }

  color: $primary;

  .p-7percent {
    padding: 0 7% 0 7%;
  }

  .underline-primary {
    border-bottom: 1px solid $primary;
  }

  .top-bottom-line-primary {
    border-bottom: 1px solid $primary;
    border-top: 1px solid $primary;
  }

  .logos-container {
    border-bottom: 4px solid $primary;
    border-top: 4px solid $primary;
  }

  .featured-image {
    height: 250px;
  }

  .home-page-content {
    padding-left: 7%;
    padding-right: 7%;

    p {
      margin: 15px 0 15px 0;
    }
    ul {
      padding-left: 40px;
      li {
        padding: 0 50px 15px 0;
      }
    }
  }

  .contact-affiliations {
    padding: 0 7% 0 7%;
  }
}

@media only screen and (max-width: 700px) {
  .p-7percent {
    padding: 0 3% 0 3%;
  }
  .home-container {
    .featured-image {
      height: 200px;
    }
    .home-page-content {
      padding-left: 1rem;
      padding-right: 1rem;

      p {
        margin: 15px 0 15px 0;
      }
      ul {
        padding-left: 20px;
        li {
          padding: 0 25px 15px 0;
        }
      }
    }
  }
}

.valuation-container {
  height: calc(100vh - 144px);
}

