.nav {
  // height: 12vw;

  .nav-container {
    color: $primary;

    li {
      display: inline-block;
    }

    li a {
      display: block;
      padding: 8px;
    }

    @media only screen and (max-width: 732px) {
      .home-link {
        display: none;
      }
    }
  }
}

